let selectedId = null;
const DigitalTransformation = () => {
  const { t, i18n } = useTranslation();
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event, id) => {
    setAnchorEl(event.currentTarget);
    selectedId = id;
  };
  const handleClose = () => {
    setAnchorEl(null);
    selectedId = null;
  };

  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;

  return (
    <Block withBackground>
      <Title>{t("Digital transformation")}</Title>

      <Grid container spacing={2}>
        {digTransformationItems.map((item, i) => (
          <React.Fragment key={i}>
            <Grid
              className={classes.iconContainerStyle}
              item
              xs={12}
              sm={6}
              md={4}
              alignItems="center"
              container
              direction="column"
            >
              <Link
                style={{ cursor: "pointer" }}
                onClick={(event) => handleClick(event, i)}
              >
                <IconInCircle Icon={item.icon} className={classes.iconStyle} />
              </Link>
              <Typography className={classes.titleIcon} align="center">
                {t(item.text)}
              </Typography>
            </Grid>
            {selectedId !== null && (
              <Popover
                id={id}
                open={open}
                anchorEl={anchorEl}
                onClose={handleClose}
                anchorOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                transformOrigin={{
                  vertical: "top",
                  horizontal: "left",
                }}
                elevation={2}
              >
                <Box maxWidth={300} p={2}>
                  {digTransformationItems[selectedId].details[
                    i18n.language
                  ].map((detail, i) => (
                    <Typography key={i}>{detail}</Typography>
                  ))}
                </Box>
              </Popover>
            )}
          </React.Fragment>
        ))}
      </Grid>
    </Block>
  );
};

export default DigitalTransformation;
